<template>
  <div>
    <div v-for="item in render_array">
      <itemText :parentdata="item" v-if="item.type=='text'" />
      <div :style="item.parent_css" style='float:left;' v-if="item.type=='text'">
        <p :id="item.id" :style="item.css" style="margin:0px;word-break: break-all;">{{item.setting.text_content}}</p>
      </div>
      <div :style="item.parent_css" style='float:left;' v-if="item.type=='textlink'">
        <a :id="item.id" :style="item.css" style='margin:0px;' :href='item.setting.url_link'>{{ item.setting.text_content}}</a>
      </div>
      <div :style="item.parent_css" style="float:left;width:100%;" v-if="item.type=='images'">
        <img :src="item.setting.image_src" :style="item.css" alt="" />
      </div>
      <div :style="item.parent_css" style="float:left;width:100%;" v-if="item.type=='images_link'">
        <a :href="item.setting.url_link" target="_blank">
          <img :src="item.setting.image_src" :style="item.css" alt="" />
        </a>
      </div>
      <div :style="item.parent_css" style='float:left;' v-if="item.type=='hellosoda'">
        <!--<button rel="external" type="button" :style="item.css" class="zocial facebook">{{ item.setting.text_content}}</button>-->
        <div id="facebook_connect" :style="item.css"></div>
        <form id="myform">
          <input type="hidden" name="application_id" value="">
          <input type="hidden" name="job_url" value="">
          <input type="hidden" name="job_id" value="">
        </form>
      </div>
      <div v-if="item.type=='fwd_popup'" v-html="item.html"></div>
  
    </div>
    <a class="xbtn1 btnskip" :href="url_main">Skip</a>
  </div>
</template>

<script>
  import itemText from './item_text.vue';
  import itemPopup from './item_popup.js';
  export default {
    components: {
      itemText
    },
    data() {
      return {
        display_object: {},
        render_wallpaper_color: {},
        render_array: [],
        url_main: "",
        url_redirect: "",
        url_redirect_fwd_max: '',
        url_redirect_gaming: '',
        url_call_back: '',
        product_id: "", //568e4591d045f29af5fadd53
        facebook_app_id: '',
        facebook_secret_id: ''
      }
    },
  
    created() {
      var vm = this;
      var api_object = vm.get_object_from_api();
  
      vm.url_redirect = api_object[0].url_redirect;
      vm.url_redirect_fwd_max = api_object[0].url_redirect_fwd_max;
      vm.url_redirect_gaming = api_object[0].url_redirect_gaming;
      vm.url_call_back = api_object[0].url_call_back;
      vm.product_id = api_object[0].product_id;
      vm.facebook_app_id = api_object[0].facebook_app_id;
      vm.facebook_secret_id = api_object[0].facebook_secret_id;
      vm.url_redirect = api_object[0].url_connect_hello_soda;
      vm.set_body_wallpaper_color(api_object[0].color_wallpaper); //vm.display_object
  
      $.each(api_object[0].items, function(i, v) { //vm.display_object
  
        if (v.type == "fwd_popup") {
          var _css = vm.convert_css_and_setting(v.css);
          var _parent_css = vm.convert_css_and_setting(v.parent_css);
          var _setting = vm.convert_css_and_setting(v.setting);
  
          vm.render_array.push({
            "id": v.id,
            "title": v.title,
            "type": v.type,
            "html": itemPopup._template(v.id, {
              value1: v.setting[0].values,
              css: vm.cssToObject(v.css),
              parent_css: vm.cssToObject(v.parent_css)
            })
          });
        } else {
  
          var _css = vm.convert_css_and_setting(v.css);
          var _parent_css = vm.convert_css_and_setting(v.parent_css);
          var _setting = vm.convert_css_and_setting(v.setting);
  
          vm.render_array.push({
            "id": v.id,
            "title": v.title,
            "type": v.type,
            "css": _css,
            "parent_css": _parent_css,
            "setting": _setting
          });
        }
  
      });
  
    },
    mounted() {
      var vm = this;
      var _what_platform = $("#what_platform").val();
      var _redirect_uri = $("#redirect_uri").val();
      vm.url_main = _redirect_uri;
      /*
      if (_what_platform == "Hello Soda") {
        vm.url_main = vm.url_redirect;
      } else if (_what_platform == "FWD Max") {
        vm.url_main = vm.url_redirect_fwd_max;
      } else if (_what_platform == "FWD Gammimg") {
        vm.url_main = vm.url_redirect_gaming;
      }*/
  
      HelloSoda.init({
        productId: vm.product_id,
        buttons: true,
        form: '#hs-form',
        events: {
          onSocialProfileAvailable(network, profile) {
            var domainx = $('meta[name="hostname"]').attr('content');
  
  
            $.ajax({
              type: "GET",
              url: domainx + "/api/fwd-hello-soda-connect/" + profile.id,
              data: profile,
              success: function(res) {
                if (res.msg_return == true) {
                  window.location.href = _redirect_uri;
                  /*
                  if (_what_platform == "Hello Soda") {
                    window.location.href = vm.url_redirect;
                  } else if (_what_platform == "FWD Max") {
                    window.location.href = vm.url_redirect_fwd_max;
                  } else if (_what_platform == "FWD Gammimg") {
                    window.location.href = vm.url_redirect_gaming;
                  }*/
                } else {
                  $.ajax({
                    type: "POST",
                    url: domainx + "/api/fwd-hello-soda-connect",
                    data: profile,
                    success: function(res) {
                      window.location.href = _redirect_uri;
                      /*if (_what_platform == "Hello Soda") {
                        window.location.href = vm.url_redirect;
                      } else if (_what_platform == "FWD Max") {
                        window.location.href = vm.url_redirect_fwd_max;
                      } else if (_what_platform == "FWD Gammimg") {
                        window.location.href = vm.url_redirect_gaming;
                      }*/
                    },
                    error: function(XMLHttpRequest, textStatus, errorThrown) {
  
                    },
                    dataType: "json"
                  });
                }
              },
              error: function(XMLHttpRequest, textStatus, errorThrown) {
  
              },
              dataType: "json"
            });
  
          },
          onJobAvailable: function(jobId) {
            console.log('The jobId is', jobId);
          }
        } //,
        //commit: true//,
        //,
        //log: 'debug'
      });
  
    },
    methods: {
      cssToObject(css) {
        let _arr = [];
        $.each(css, function(i2, v2) {
          let _css = "";
          var arx = ["width", "height", "font-size"];
          var n = arx.indexOf(v2.key);
          var check_px = v2.values.split("px");
          if (check_px.length == 2) {
            _css += v2.key + ":" + (parseInt(check_px[0]) * 1) + "px;";
          } else {
            _css += v2.key + ":" + (v2.values) + ";";
          }
          _arr.push(_css);
        });
        return _arr;
      },
      get_object_from_api() {
        var api_object_string = $("#api_values").html();
        $("#api_values").remove(); //TODO
        var api_object = JSON.parse(api_object_string);
        return api_object;
      },
      set_body_wallpaper_color(color) {
        $("body").css("background-color", color);
      },
      convert_css_and_setting(v) {
        var css_string = "{";
        $.each(v, function(i2, v2) {
          if (i2 > 0) {
            css_string += ",";
          }
          css_string += `"${v2.key}":"${v2.values.toString().replace(/"/g, '\\"')}"`
          //css_string += "\"" + v2.key + "\":\"" + v2.values + "\"";
        });
        css_string += "}";
        return JSON.parse(css_string);
      }
    }
  }
</script>

<style>
  .xbtn1 {
    border-radius: 3px;
    padding: 10px 30px;
    border-width: 1px;
    border-style: solid;
    cursor: pointer;
    letter-spacing: 1px;
    font-weight: 700;
    outline: none;
    position: relative;
  }
  
  .btnskip {
    background: #fcad26;
    color: #fff;
    position: absolute;
    top: 0;
    right: 0;
  }
</style>



