let _box = `
float: left;
height: 100%;
clear: none;
width: 100%;
background-color: rgba(0, 0, 0, 0.490196);
min-width: 0px;
min-height: 0px;
position: absolute;
top:0px;
left: 0px;
display:none;`;

let _box1 = `
max-width: 90%;
float: none;
height: auto;
margin: 35% auto;
clear: none;
width: 412px;
background-color: #e06c22;
min-height: 40px;
padding: 20px;`;

let _box4 = `
float: none;
height: auto;
margin: 0px auto;
clear: both;
width: 80px;
border: 1px solid rgb(255, 255, 255);
border-radius: 8px !important;
cursor:pointer;
padding: 10px;`

let _text1 = `
float: left;
font-size: 1em;
width: 100%;
height: auto;
text-align: center;
font-weight: normal;
line-height: 1em;
margin: 0px;
clear: none;
min-height: 0px;
min-width: 0px;
color: rgb(255, 255, 255);`

let _box2 = `
float: none;
height: auto;
margin: 0px auto;
clear: none;
width: 100%;
min-width: 0px;`

let _text = `
float: left;
width: auto;
height: auto;
text-align: left;
font-weight: 100;
line-height: 30px;
margin: 0px;
clear: none;
min-height: 0px;`

let _box3 = `
float: left;
height: auto;
margin: 0px;
clear: none;
width: 100%;
min-width: 0px;
padding: 10px;`

export default {
  _template : function (new_id,value) {
    return `
<div style="float:left;width:100%;${value.parent_css[0]}">
    <p style="${value.css[2]}"><a href="javascript:$('#${new_id}_contraniner').show()" style="text-decoration: underline;${value.css[0]}${value.css[1]}">เงื่อนไขและข้อตกลง</a></p>
    <div style="${_box}" class="clearfix" id="${new_id}_contraniner">
        <div style="${_box1}" class="clearfix">
            <div style="${_box2}" class="clearfix">
                <p style="${_text}${value.parent_css[1]}${value.parent_css[2]}" id="${new_id}">${value.value1}</p>
            </div>
            <div style="${_box3}" class="clearfix">
                <div style="${_box4}" class="clearfix" onclick="$('#${new_id}_contraniner').hide()">
                    <p style="${_text1}" >ปิด</p>
                </div>
            </div>
        </div>
    </div>
</div>
`;
  }
}



